.DistrictSelector {
    display: flex;
    background-color: black;
    width: 200px;
    height: 100px;
    border: 1px solid coral;
    border-radius: 8px;
    color: aliceblue;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 10px;
  }
  
  .DistrictSelector h1 {
    font-size: 16px;
    color: aliceblue;
    text-align: center;
  }

  #districtSelect {
    font-size: 16px;
    width: 40%;
  }